import { Component, OnInit } from '@angular/core';
import { UserService } from 'src/app/api/controllers/User';
import { ApartmentService } from 'src/app/api/controllers/Apartment';
import { AlertService } from 'src/app/api/controllers/Alert';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';
import * as moment from 'moment';

@Component({
  selector: 'app-security-reports',
  templateUrl: './security-reports.component.html',
  styleUrls: ['./security-reports.component.scss']
})
export class SecurityReportsComponent implements OnInit {

  blockData:any;

  report:any;

  securityCategoryData: any;
  alertTypeStatus: any;

  ItemStartIndex:any = 0;
  ItemEndIndex:any;
  totalItems:any;
  itemLimit:any = 8;

  unitFieldType:string = "unitno";
  unitOrder:boolean = false;

  isReportSubmitted:boolean = true;
  isDataLoaded:boolean = true;

  alertReportsDataList:any;
  reportsData:any;

  constructor(
  	private apartmentService:ApartmentService,
    private alertService:AlertService,
  	private lookupService:LookupService,
  	private cookieService:CookieService
  ) { }

  getIndexParams(event){
    this.ItemStartIndex = event.ItemStartIndex;
    this.ItemEndIndex = event.ItemEndIndex;
    this.itemLimit = event.itemLimit;
  }

  sortUnitData(type){
    this.unitFieldType = type;
    this.unitOrder = !this.unitOrder;
  }

  getFieldOrderBy(type) {
    if(this.unitFieldType == type){
      return this.unitOrder ? 'desc' : 'asc';
    }
    else return '';
  }


  isMobileView() {
    return window.innerWidth <= 767 ? 'table-responsive' : '';
  }

  isItemsAvailable(){
    return this.totalItems > 0 ? true : false;
  }

  isNoItemsAvailable(){
    return this.totalItems == 0 ? true : false;
  }

  getDate(date){
    return moment(date).format("MM-DD-YYYY");
  }

  getDateFormat(date){
    if(date){
      return moment(date).format("YYYY-MM-DD");
    }else{
      return undefined
    }
  }

  getTimeFormat(dateTime){
    return moment(dateTime).format("YYYY-MM-DD HH:mm");
  }

  submitSecurityAlertReports(form) {
  	this.isReportSubmitted = true;
    this.isDataLoaded = false;

  	let details = {
  	  ApartmentBlockID: parseInt(this.report.apartmentBlockID),
  	  StartDate: this.getDateFormat(this.report.startDate),
  	  EndDate: this.getDateFormat(this.report.endDate),
      ApartmentID: parseInt(this.cookieService.get('apartmentId')),
      EmergencyCategoryTypeID: parseInt(this.report.emergencyCategoryTypeID),
      AlertTypeStatusID: parseInt(this.report.alertTypeStatusID)
    }


  	this.alertService.getReportsForSecurityPanicAlertMultiFilter(details).subscribe((res:any) => {

    	this.alertReportsDataList = res;
    	this.totalItems = this.alertReportsDataList.length;

		if(this.totalItems>this.itemLimit){
			this.ItemEndIndex = this.itemLimit;
		}
		else {
			this.ItemEndIndex = this.totalItems;
		}

		this.isDataLoaded = true;
		this.isReportSubmitted = false;

    },
    error => {

    });

  }

  ngOnInit() {

  	this.report = {};
    this.report.apartmentID = "";
  	this.report.apartmentBlockID = "";
    this.report.emergencyCategoryTypeID = "";
    this.report.alertTypeStatusID = "";
    this.report.startDate = undefined;
    this.report.endDate = undefined;
    let apartment_params = {
      apartmentId: parseInt(this.cookieService.get('apartmentId'))
    }
  	this.apartmentService.getApartmentBlockByApartmentId(apartment_params).subscribe((res:any) => {
      this.blockData = res;
    });
    let sec_params = {
      LookupTypeId: 29
    }
    this.lookupService.getLookupValueByLookupTypeId(sec_params).subscribe((res:any) => {
      this.securityCategoryData = res.filter(item => {
          return item.isActive;
      });

    });
    let alert_params = {
      LookupTypeId: 30
    }
    this.lookupService.getLookupValueByLookupTypeId(alert_params).subscribe((res:any) => {
      this.alertTypeStatus = res.filter(item => {
          return item.isActive;
      });

    });
  }

}
