import { Component, OnInit } from '@angular/core';

import { UserService } from 'src/app/api/controllers/User';
import { ApartmentService } from 'src/app/api/controllers/Apartment';
import { AlertService } from 'src/app/api/controllers/Alert';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';

import * as moment from 'moment';
import { CommonConfirmModalComponent, ConfirmDialogModel } from 'src/app/shared/components/common-confirm-modal/common-confirm-modal.component';
import { MatDialog } from '@angular/material/dialog';
import { SharedService } from 'src/app/shared/services/shared.service';
import { SharedToasterService } from 'src/app/shared/services/shared-toaster.service';

@Component({
  selector: 'app-security-history',
  templateUrl: './security-history.component.html',
  styleUrls: ['./security-history.component.scss']
})
export class SecurityHistoryComponent implements OnInit {
  alert: any;

  alertList: any;

  isAlertsLoaded: boolean = false;
  isAlertUpdated: boolean = false;

  apartmentBlockUnitId: number;

  emergencyTypeList: any;
  alertTypeList: any;

  apartmentBlockName: any;
  apartmentBlockUnitName: any;

  userDetails: any = [];
  blockDetails: any = [];
  locationDetails: any = [];

  result: string = '';
  confirmationMessage: string;

  columnData: any;

  gridSourceData:any;

  historyData: any;

  tableData = {
    alertlist : []
  }


  constructor(
    private userService: UserService,
    private apartmentService: ApartmentService,
    private alertService: AlertService,
    private lookupService: LookupService,
    private sharedService: SharedService,
    private cookieService: CookieService,
    public dialog: MatDialog,
    private SharedToaster: SharedToasterService,
  ) { }

  getEmergencyType(id) {

    //medical
    if (id == 130) {
      return 'medical';
    }
    else if (id == 131) {
      return 'theft';
    }
    else if (id == 132) {
      return 'fire';
    }
    else if (id == 133) {
      return 'shooter';
    }
    else if (id == 134) {
      return 'police';
    }
    else {
      return 'other';
    }
  }

  getAlertName(id) {

    var data = _.filter(this.emergencyTypeList, function (item) {
      if (item.lookupValueId === id)
        return item;
    });


    if (data === undefined || data.length == 0) {
      return '';
    }
    else {
      return data[0].lookupValueName.toLowerCase();
    }

  }

  getAlertStatus(id) {

    var data = _.filter(this.alertTypeList, function (item) {
      if (item.lookupValueId === id)
        return item;
    });

    if (data === undefined || data.length == 0) {
      return '';
    }
    else {
      return data[0].lookupValueName.toLowerCase();
    }

  }

  getBlock(id) {


    var data = _.filter(this.blockDetails, function (item) {
      if (item.apartmentBlockUnitId === id)
        return item;
    });

    if (data === undefined || data.length == 0) {
      return '';
    }
    else {
      return data[0].apartmentBlockNumber + " " + data[0].apartmentBlockUnitNumber;
    }

  }

  getUserName(id) {


    var data = _.filter(this.userDetails, function (item) {
      if (item.userId === id)
        return item;
    });

    if (data === undefined || data.length == 0) {
      return '';
    }
    else {
      return data[0].firstName;
    }

  }

  getContactNumber(id) {


    var data = _.filter(this.userDetails, function (item) {
      if (item.userId === id)
        return item;
    });

    if (data === undefined || data.length == 0) {
      return '';
    }
    else {
      return data[0].phoneNumber;
    }

  }

  getDate(date) {
    return moment(date).format("MM/DD/YYYY, h:mm A");
  }

  getDateTime(date) {
    return moment(date).format("MM/DD/YYYY, h:mm A");
  }

  isOpenStatus(id) {
    return id == 135 ? true : false; // 135 - open status
  }

  isInProgressStatus(id) {
    return id == 136 ? true : false; // 135 - inprogress status
  }

  isClosedStatus(id) {
    return id == 137 ? true : false; // 135 - closed status
  }

  getLatitude(id) {

    var data = _.filter(this.locationDetails, function (item) {
      if (item.userId === id)
        return item;
    });

    if (data === undefined || data.length == 0) {
      return '';
    }
    else {
      var map = data[0].coords;
      var latitude = map.split(',');
      return parseFloat(latitude[0]);

    }

  }

  getLongitude(id) {

    var data = _.filter(this.locationDetails, function (item) {
      if (item.userId === id)
        return item;
    });

    if (data === undefined || data.length == 0) {
      return '';
    }
    else {
      var map = data[0].coords;
      var longitude = map.split(',');
      return parseFloat(longitude[1]);
    }

  }

  isNoAlerts() {
    return this.alertList.length == 0 ? true : false;
  }

  ngOnInit() {

    var cellsrenderer = (row, column, value) => {
      return '<div class="jqx-custom-inner-cell">' + value + '</div>';
    }

    var columnrenderer = (value) => {
      return '<div style="padding: 14px">' + value + '</div>';
    }


    this.columnData = [{
      text: 'Category Name',
      datafield: 'lookupValueName',
      width: 100,
      pinned: true,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    }, {
      text: 'Date',
      datafield: 'insertedOn',
      cellsrenderer: cellsrenderer,
      minwidth: 170,
      renderer: columnrenderer
    }, {
      text: 'Block Number',
      datafield: 'apartmentBlockNumber',
      cellsrenderer: cellsrenderer,
      minwidth: 170,
      renderer: columnrenderer
    },
    {
      text: 'Block Unit Number',
      datafield: 'apartmentBlockUnitNumber',
      cellsrenderer: cellsrenderer,
      minwidth: 170,
      renderer: columnrenderer
    },
    {
      text: 'Comments',
      datafield: 'notes',
      cellsrenderer: cellsrenderer,
      minwidth: 170,
      renderer: columnrenderer
    }, {
      text: 'Person Name',
      datafield: 'firstName',
      cellsrenderer: cellsrenderer,
      minwidth: 170,
      renderer: columnrenderer
    }, {
      text: 'Phone Number',
      datafield: 'phoneNumber',
      cellsrenderer: cellsrenderer,
      minwidth: 170,
      renderer: columnrenderer
    }, {
      text: 'Assigned to',
      datafield: 'assignedTo_Label',
      cellsrenderer: cellsrenderer,
      minwidth: 150,
      renderer: columnrenderer
    }, {
      text: 'Requested By',
      datafield: 'raisedby_Label',
      cellsrenderer: cellsrenderer,
      minwidth: 150,
      renderer: columnrenderer
    }]


    this.alert = {};
    this.alert.gpsLocation = " ";
    this.alert.notes = " ";

    let params = {
      apartmentId: parseInt(this.cookieService.get('apartmentId'))
    }

    this.alertService.getAllApartmentBlockUnitAlertByApartmentId(params).subscribe((alert: any) => {

      this.alertList = alert.filter(item => {
      return (item.alertStatusId == 137);
      });

      this.isAlertsLoaded = true;
      console.log(this.alertList);


      _.each(this.alertList, item => {

        let params = {
          userid: parseInt(item.insertedBy)
        }

        this.userService.getUserById(params).subscribe((user: any) => {

          this.userDetails.push(user[0]);
          //console.log(this.userDetails);

        },
          error => {
            console.log(error);
          });

        let apartmentBlockUnitIdParam = {
          apartmentBlockUnitId: item.apartmentBlockUnitId
        }
        this.apartmentService.getApartmentBlockUnitById(apartmentBlockUnitIdParam).subscribe((user: any) => {

          this.blockDetails.push(user[0]);

        },
          error => {
            console.log(error);
          });

        if (item.gpslocation === null || item.gpslocation === undefined || item.gpslocation.length == 0) {
          item.gpslocation = "47.4925,19.0513";
        }
        this.locationDetails.push({ userId: item.insertedBy, coords: item.gpslocation });
        // console.log(this.locationDetails);

      });
      this.tableData.alertlist = this.alertList;
      console.log("Table data  == ", this.tableData);
      this.gridSourceData = {
        localdata: this.tableData,
        datatype: "array"
      }
      this.historyData = new jqx.dataAdapter(this.gridSourceData);

    },
      error => {
        console.log(error);
      }
    );

    let emergencyParams = {
      LookupTypeId: 29
    }

    //get emergency types
    this.lookupService.getLookupValueByLookupTypeId(emergencyParams).subscribe((res: any) => {

      this.emergencyTypeList = res;
      // this.gridSourceData = {
      //   localdata: this.emergencyTypeList,
      //   datatype: "array"
      // }
      // this.historyData = new jqx.dataAdapter(this.gridSourceData);
    });

    let alertParams = {
      LookupTypeId: 30
    }

    //get alert status types
    this.lookupService.getLookupValueByLookupTypeId(alertParams).subscribe((res: any) => {
      this.alertTypeList = res;

    });


  }

}

function getClassName(value) {
  var name = value.split('-');
  return name[0].toLowerCase();
}
(window as any).getClassName = getClassName;

function showConfirmDeleteEvent(row) {
  var event = new CustomEvent('onTicketDelete', {
    detail: {
      rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).showConfirmDeleteEvent = showConfirmDeleteEvent;


function editTicketEvent(row) {
  var event = new CustomEvent('onEditTicket', {
    detail: {
      rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).editTicketEvent = editTicketEvent;