import { Component, OnInit } from '@angular/core';
import { UserService } from 'src/app/api/controllers/User';
import { ApartmentService } from 'src/app/api/controllers/Apartment';
import { AlertService } from 'src/app/api/controllers/Alert';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { CookieService } from 'ngx-cookie-service';
import { SharedService } from 'src/app/shared/services/shared.service';


import * as _ from 'underscore';
import * as moment from 'moment';

@Component({
  selector: 'app-security-setup',
  templateUrl: './security-setup.component.html',
  styleUrls: ['./security-setup.component.scss']
})
export class SecuritySetupComponent implements OnInit {

    securityCategoryData:any;

    isSecurityCategoryLoaded:boolean = false;
    isSecurityCategorySubmitted:boolean = true;

    isSecurityCategoryNew:boolean = false;
    isSecurityCategoryUpdate:boolean = false;

    securityCategory:string = "";
    securityCategoryUpdateId:number = 0;

    alertTicketMessage:string = "";
    isCategoryError:boolean = false;
    isCategorySuccess:boolean = false;

    securityDataList:any;

    constructor(
      private alertService: AlertService,
    	private lookupService: LookupService,
      private sharedService:SharedService,
      private cookieService:CookieService
    ) { }

    addNewSecurityCategory(){
    	this.isSecurityCategoryNew = true;
    	this.isSecurityCategoryUpdate = false;
    }

    updateSecurityCategory(item){
    	this.isSecurityCategoryUpdate = true;
    	this.isSecurityCategoryNew = false;
    	this.securityCategory = item.lookupValueName;
    	this.securityCategoryUpdateId = item.lookupValueId;
    }

    deleteSecurityCategory(item, index){

    	this.isSecurityCategoryLoaded = false;

    	var params = {
        lookupValueId: item.lookupValueId,
        updateUserId: parseInt(this.cookieService.get('userId'))
      }

    	this.lookupService.deleteLookupvalue(params).subscribe((res:any) => {

    	  this.securityCategoryData.splice(index, 1);
  	  this.isSecurityCategoryLoaded = true;

  	});

    }

    removeCategoryBox(){
    	this.isSecurityCategoryNew = false;
    	this.isSecurityCategoryUpdate = false;
    	this.isCategoryError = false;
    }

    getSecurityList(id){

      var data = _.filter( this.securityDataList, function( item ) {
        if(item.securityCategoryId === id)
          return item;
      });

      if(data === undefined || data.length == 0){
        return 0;
      }
      else {
        return data.length;
      }

    }

    submitSecurityCategoryForm(form) {

    	this.isSecurityCategorySubmitted = false;

    	if(this.isSecurityCategoryNew) {

    		let details = {
        "apartmentId": parseInt(this.cookieService.get('apartmentId')),
  		  "lookupTypeId": 29, // security category lookup id
  		  "lookupValueName": this.securityCategory,
  		  "description": this.securityCategory,
  		  "isActive": true,
        "insertedBy": parseInt(this.cookieService.get('userId')),
        "insertedOn": new Date().toISOString(),
        "updatedBy": null,
        "updatedOn": new Date().toISOString(),

  		}
       let add_param= {
         lookupvalue:details
       }
  		this.lookupService.addLookupValue(add_param).subscribe((res:any) => {

  	  		if(res.message) {
            let param = {
              LookupTypeId: 29
            }
  	  			this.lookupService.getLookupValueByLookupTypeId(param).subscribe((res:any) => {

  	  				this.isSecurityCategorySubmitted = true;
  		  			this.isCategorySuccess = true;
              this.securityCategory =  undefined;
  		          	this.alertTicketMessage = "Category Added Successfully!";
  		          	this.securityCategoryData = res.filter(item => {
  	                	return item.isActive;
  	              	});
  	  			});
  	  		}
  	  		else {
  	  			this.isSecurityCategorySubmitted = true;
  	        this.isCategoryError = true;
  	        this.alertTicketMessage = res.errorMessage;
  	  		}
    		});
    	}

    	else {

    		let details = {

    		  "lookupValueId": this.securityCategoryUpdateId,
          "apartmentId": parseInt(this.cookieService.get('apartmentId')),
    		  "lookupTypeId": 29, // security category lookup id
    		  "lookupValueName": this.securityCategory,
    		  "description": this.securityCategory,
    		  "isActive": true,
          "insertedBy": parseInt(this.cookieService.get('userId')),
          "insertedOn": new Date().toISOString(),
          "updatedBy": null,
          "updatedOn": new Date().toISOString(),

  		}
      let update_param= {
        lookupvalue:details
      }
  		this.lookupService.updateLookupValue(update_param).subscribe((res:any) => {

  	  		if(res.message) {
            let param = {
              LookupTypeId: 29
            }
  	  			this.lookupService.getLookupValueByLookupTypeId(param).subscribe((res:any) => {
  	  				this.isSecurityCategorySubmitted = true;
  		  			this.isCategorySuccess = true;
  		          	this.alertTicketMessage = "Category Updated Successfully!";

  		          	this.securityCategoryData = res.filter(item => {
  	                	return item.isActive;
  	              	});
  	  			});
  	  		}
  	  		else {

  	  			this.isSecurityCategorySubmitted = true;
  	        this.isCategoryError = true;
  	        this.alertTicketMessage = res.errorMessage;
  	  		}
    		});
    	}
    }

    ngOnInit() {
      let param = {
        LookupTypeId: 29
      }
    	this.lookupService.getLookupValueByLookupTypeId(param).subscribe((res:any) => {

		      this.isSecurityCategoryLoaded = true;

    		this.securityCategoryData = res.filter(item => {
          	return item.isActive;
        });

    	});

      // // this.securityService.getAllSecurityByApartmentId(parseInt(this.cookieService.get('apartmentId'))).subscribe((res:any) => {
      // //
      // //   this.securityDataList = res.filter(item => {
      // //       return item.isActive;
      // //   });
      //
      // });

    }
  }
